<template>
  <div class="host">
    <div class="row">
      <div class="dropdown mb-1 col-sm-12">
        <button
          class="btn btn-light dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Color: {{ colorNames[condition.color] }}
        </button>
        <div class="dropdown-menu">
          <a
            class="dropdown-item plain"
            href="javascript:void(0)"
            v-for="(c, i) in allowedColors"
            :key="'c' + i"
            @click="condition.color = colors.indexOf(c)"
          >
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
            <font-awesome-icon :icon="faSquareFull" :style="{ color: c }" />
            {{ colorNames[i + 7] }}
          </a>
        </div>
      </div>
      <div class="dropdown mb-1 col-sm-12">
        <button
          class="btn btn-light dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Icon: {{ iconNames[condition.icon] }}
        </button>
        <div class="dropdown-menu">
          <a
            class="dropdown-item plain"
            href="javascript:void(0)"
            v-for="(c, i) in allowedIcons"
            :key="'i' + i"
            @click="condition.icon = icons.indexOf(c)"
          >
            <font-awesome-icon :icon="c" class="mr-1" fixed-width />
            {{ iconNames[i + 7] }}
          </a>
        </div>
      </div>
      <div class="input-group mb-1 col-sm-12">
        <input
          type="text"
          class="form-control"
          placeholder="e.g. Curfew at 9pm"
          v-model="condition.name"
        />
      </div>
    </div>
    <textarea
      class="form-control mt-1 mb-4"
      placeholder="e.g. All residents have to stay home after 9pm."
      style="height: 100px"
      v-model="condition.more"
    ></textarea>
  </div>
</template>

<script>
import globals from "@/components/globals.js";
import { faSquareFull } from "@fortawesome/free-solid-svg-icons";
const $ = window.$;

export default {
  props: ["cond"],
  data() {
    return {
      shown: false,
      colors: globals.colors,
      colorNames: globals.colorNames,
      icons: globals.icons,
      iconNames: globals.iconNames,
      condition: this.cond,
      faSquareFull
    };
  },
  computed: {
    allowedColors() {
      return this.colors.slice(7);
    },
    allowedIcons() {
      return this.icons.slice(7);
    }
  },
  methods: {
    toggle() {
      $(this.$refs.accordion).collapse("toggle");
    }
  },
  watch: {
    cond(val) {
      this.condition = val;
    }
  }
};
</script>

